figure {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding: 0 1rem; */
}

figure img {
    width: 90%;
}

figure a {
    display: flex;
    justify-content: center;
    width: max-content;
}

@media screen and (max-width: 576px) {
    figure {
        width: 100%;
    }
}