h2 {
    font-family: 'Ubuntu';
}

p {
    font-family: 'Montserrat';
}

.project {
    padding: 2rem;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 5rem;
}

.project:nth-child(even) {
    display: flex;
    flex-direction: row-reverse;
}

.project-body {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 1rem;
}


.project-desc {
    padding: 0 1rem;
}

.project-desc h2 {
    font-weight: 400;
    font-size: 2rem;
    margin-bottom: 1rem;
}

.project-desc p {
    line-height: 1.5;
    margin-bottom: 0.5rem;
}

.project-container {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
}

.project-img {
    padding: 0 1rem;
    
}

.project-img h3 {
    font-family: 'Ubuntu';
    font-weight: 400;
    margin-bottom: 0.5rem;
}

.project-img img {
    width: 35px;
    margin-right: 1rem;
}

#boostrap {
    width: 55px;
}

@media screen and (max-width: 1040px) {
    .project {
        margin-bottom: 4rem;
    }

    .project-desc h2 {
        font-size: 1.5rem;
        margin-bottom: 0.5rem;
    }

    .project-desc p {
        font-size: 1rem;
    }
}

@media screen and (max-width: 576px) {
    .project {
        padding: 1rem;
        flex-wrap: wrap;
    }

    .project-body {
        width: 100%;
        padding: 1rem 0.5rem;
    }

    .project-desc {
        padding: 0;
    }

    .project-desc h2 {
        font-size: 1.1rem;
    }

    .project-desc p {
        font-size: 0.8rem;
    }

    .project-img {
        padding: 0;
    }

    .project-img h3 {
        font-size: 1rem;
    }

    .project-img img {
        width: 30px;
    }
}