#my-projects {
  padding-top: 10em;
}

.container-projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 3em;
  margin-top: 2em;
  overflow-x: hidden;
}

/* .container-projects a {
  width: 25%;
} */

.view-more {
  font-family: 'Montserat', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2em;
}

.view-more a {
  color: #6246ea;
  text-decoration: none;
}

.boostrap {
  width: 60px;
}

@media screen and (max-width: 1040px) {
  #my-projects {
    padding-top: 6rem;
  }
  
  .container-projects {
    gap: 1rem;
  }
}

@media screen and (max-width: 576px) {
  #my-projects {
    padding-top: 4em;
  }

  .container-projects a {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .view-more {
    margin: 0 1rem;
    font-size: 0.8rem;
  }

  .view-more a {
    font-size: 0.8rem;
  }
}